body {
  font-size: 1.5rem;
}

.input {
  
}

.rectangle {
  top: 0;
  left: 0;
  // position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 150px;
  height: 150px;
  background: linear-gradient(45deg, rgb(205, 37, 152), rgb(255, 29, 236) );
  border-radius: 20px;
  &_wrapper {
    width: 200px;
    height: 200px;
    top: 0;
    left: 0;
    position: absolute
  }
}

.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 600px;
  width: 800px;
}

.cards {
  display: grid;
  grid-template-columns: repeat(3, 200px);
  grid-template-rows: repeat(3, 100px);
  gap: 20px;
  position: relative;
}
.cards__main-card {
  background: linear-gradient(-45deg, rgb(69, 69, 69), rgb(126, 126, 126));
  border-radius: 10px;
  transform: translateY(120%) translateX(110%);
  position: absolute;
  width: 200px;
  height: 100px;
  cursor: pointer;
  z-index: 100;
}

.cards_secondary-cards {
  cursor: pointer;
  background: linear-gradient(-45deg, rgb(27, 27, 27), rgb(47, 47, 47));
  border-radius: 10px;
  
}

.drag-rect {
  width: 50px;
  height: 50px;
  background-color: pink;

  border-radius: 10px;
  position: absolute;
  top: calc(50% - 25px);
  left: calc(50% - 25px);
  touch-action: none;
  opacity: 1;
  &__wrapper {
    width: 900px;
    height: 300px;
    border-radius: 50px;

    border: 1px solid black;
    display: flex;
    position: relative;
    // overflow: hidden;
  }

}


.slider {
  width: 200px;
  height: 50px;
  margin: 20px auto 0 auto;
  position: relative;
  &_container {
    user-select: none;
    touch-action: none;
    display: flex;
    align-items: center;
    color: white;
    justify-content: center;
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: rgb(56, 56, 56);

    border-radius: 10px;

  }
  &_left-side {
    opacity: 0;
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: -1;
    transform: scale(0.9);
    background: linear-gradient(90deg, yellow, orange);
    transition: opacity 0.3s;
    border-radius: 12px;
    &__circle__wrapper {
      height: 100%;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      
    }
    &__circle {
      background-color: white;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      margin-left: 20px;
    }

  }
  &_right-side {
    // opacity: 0;
    opacity: 0;
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: -1;
    transform: scale(0.9);
    background: linear-gradient(90deg, blue, green);
    transition: opacity 0.3s;
    border-radius: 12px;
    &__circle__wrapper {
      height: 100%;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      
    }
    &__circle {
      background-color: white;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      margin-right: 20px;
    }
  }
  .active {
    opacity: 1 !important;
    transition: opacity 1s;

  }
}